import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { FaDoorOpen, FaFilm } from 'react-icons/fa';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


class Add extends Component {
    
    constructor(props) {
        super(props);
        this.initiateState = {
            id:-1,
            titre:"",
            realisateur:"",
            nationalite:"",
            genre:"",
            synopsis:"",
            acteurs:"",
            prix:0,
            image:"",
            disponible:false 
         }
         this.state = this.initiateState;
    }

    handleChange = (e)=>{
      
        const {name, type, checked, value} = e.target;
        const val = type === 'checkbox' ? checked : value;
        this.setState({[name]:val});
        //console.log(val)
    }

    handleSubmit=(e)=>{
        e.preventDefault();
        console.log(this.state)
        
        let img='';
        if(this.state.image){
            
            img = this.state.image.match(/[a-zA-Z0-9-_]+\.(jpg|png)/)[0]; 
        }

        let newMovie = {
            titre: this.state.titre,
            realisateur: this.state.realisateur,
            nationalite: this.state.nationalite,
            genre: this.state.genre,
            prix: this.state.prix,
            synopsis: this.state.synopsis,
            acteurs: this.state.acteurs,
            image: img,
            disponible: this.state.disponible,
        }
        this.props.getFilms(newMovie);
        this.setState(this.initiateState) 
        toast('film ajouté avec succès!', {className: 'bg-success text-white fw-bold'});
    }
    
      

    render() { 
        console.log(this.props)
        const {titre, realisateur, nationalite, genre, prix, disponible, synopsis, acteurs}= this.state;
        
        return ( 
            <>
                <div className="row mb-5">
                    <div className="col-8 offset-2">
                        <div className="card">
                            <div className="card-header text-center bg-success h1 "> Ajouter un film <FaFilm/></div>
                            <div className="card-body">
                            <form className="row g-3 needs-validation" onSubmit={this.handleSubmit}>
                            <div className="col-md-6">
                                <label htmlFor="titre" className="form-label">Titre</label>
                                <input type="text" required  className="form-control" id="titre" name="titre" value={titre} onChange={this.handleChange} placeholder="Entrer votre titre" />
                               
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="realisateur" className="form-label">Réalisateur</label>
                                <input type="text" required className="form-control" id="realisateur"  name="realisateur" value={realisateur} onChange={this.handleChange}  placeholder="Entrer votre réalisateur" />
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="nationalite" className="form-label">Nationalité</label>
                                <input type="text" required className="form-control" id="nationalite" name="nationalite" value={nationalite} onChange={this.handleChange}  placeholder="Entrer la nationnalité du film" />
                               
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="genre" className="form-label">Genre</label>
                                <input type="text" required className="form-control" id="genre"  name="genre" value={genre} onChange={this.handleChange} placeholder="Entrer le genre du film" />
                            </div>
                            <div className="col-md-12">
                                 <label htmlFor="synopsis" className="form-label">Synopsis</label>
                                  <textarea type="text" onChange={this.handleChange} value={synopsis} className="form-control" id="synopsis" name="synopsis" placeholder="Synopsis de votre film" required />
                            </div>
                            <div className="col-md-12">
                                 <label htmlFor="acteurs" className="form-label">Acteurs</label>
                                  <textarea type="text" onChange={this.handleChange} value={acteurs} className="form-control" id="acteurs" name="acteurs" placeholder="Entrez les acteurs principaux" required />
                            </div>
                            <div className="col-md-6 offset-3">
                                <label htmlFor="prix" className="form-label">Prix</label>
                                <input type="number" required className="form-control" id="prix"  name="prix" value={prix} onChange={this.handleChange} placeholder="Entrer votre prix" />
                            </div>
                            
                            <div className="col-md-12">
                                <label htmlFor="prix" className="form-label">image</label>
                                <input type="file" required className="form-control" id="image" name="image" onChange={this.handleChange}  />
                            </div>
                            
                            <div className="col-md-6">
                                <div className="form-check">
                                <input className="form-check-input" type="checkbox" id="disponible" name="disponible" checked={disponible} onChange={this.handleChange}  />
                                <label className="form-check-label" htmlFor="Disponible">
                                    Disponible
                                </label>
                                </div>
                            </div>
                            <div className="col-12">
                                <button className="btn btn-success col-4 offset-4 h4" type="submit">Soumettre!</button>
                            </div>
                            </form>
                                
                            </div>
                        </div>
                        <Link to="/admin123/reload"><FaDoorOpen size="25"/>Retour Office</Link>

                    </div>
                </div>
                <ToastContainer
                                 position="top-right"
                                 autoClose={2000}
                                 hideProgressBar={false}
                                 newestOnTop={false}
                                 closeOnClick
                                 rtl={false}
                                 pauseOnFocusLoss
                                 draggable
                                 pauseOnHover
                 />
            </>
         );
    }
}
 
export default Add;



