import React from 'react';
import { FaRegTimesCircle, FaRegCheckCircle, FaTrashAlt, FaRegEdit } from "react-icons/fa";
import { Scrollbars } from 'rc-scrollbars';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const Listo = (props) => {
    return ( 
        <>
        <table className="table table-striped table-hover text-center">
            <thead className="table-primary">
                <tr>
                    <th>Id</th>
                    <th>Titre</th>
                    <th>Réalisateur</th>
                    <th>Nationalité</th>
                    <th>Genre</th>
                    <th>Image</th>
                    <th>Synopsis</th>
                    <th>Acteurs</th>
                    <th>Prix</th>
                    <th>Disponible</th>
                    <th colSpan="2">Actions</th>

                </tr>
            </thead>
            <tbody>
                    {props.lines.map((line, index)=>{
                        return(
                            <tr key={index}>
                                <td>{line.id}</td>
                                <td>{line.titre}</td>
                                <td>{line.realisateur}</td>
                                <td>{line.nationalite}</td>
                                <td>{line.genre}</td>
                                <td><img src={`images/${line.image}`} alt="" width="80"/></td>
                                <td><Scrollbars style={{ width: 300, height: 100 }}>
                                    {line.synopsis}
                                    </Scrollbars>
                                </td>
                                <td><Scrollbars style={{ width: 100, height: 100 }}>
                                    {line.acteurs}
                                    </Scrollbars>
                                </td> 
                                <td>{line.prix}€</td>
                                <td>{(line.disponible)
                                        ? <FaRegCheckCircle className="text-info" size="24"/> 
                                        : <FaRegTimesCircle className="text-secondary" size="24"/>}
                                </td>
                                <td>
                                    <button onClick={()=>{props.handleRecorrect(index)}} className="btn btn-warning"><FaRegEdit/></button>
                                </td>
                                <td>
                                    <button onClick={()=>{
                                                            if(window.confirm('Etes vous sûr de supprimer ce film ?')){
                                                                props.handleDelete(index)
                                                                toast('Film supprimé avec succès',  {className: 'bg-info text-white fw-bold'})
                                                            }else{
                                                                console.log('Vous n\'avez pas supprimé ce film ');
                                                            }
                                                            }} className="btn btn-danger"><FaTrashAlt/></button>
                                </td>

                            </tr>
                        
                            )
                    })}
            </tbody>
        </table>
        <ToastContainer
                        position="top-right"
                        autoClose={2000}
                        hideProgressBar={false}
                        newestOnTop={false}
                        closeOnClick
                        rtl={false}
                        pauseOnFocusLoss
                        draggable
                        pauseOnHover
        
        />        
        </>
     );
}
 
export default Listo;