import React, { Component } from 'react';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            login:'',
            pass:'',
            loginError:'',
            passError:'',
            errorMessage:''
         }
    }
    handleChange=(e)=>{
        const {name, value} = e.target;
        let val = value;

        let err1 = '', err2 = '';
        if(name === "login"){
            if(val === ''){
                err1 = <strong>Le champs login est requis</strong>
            }
        }else if(name === "pass"){
            if(val === ''){
                err2 = <strong>Le champs mot de passe est requis</strong>
            }
        }
        this.setState({loginError:err1, passError:err2})
        this.setState({[name]: val})//[] pour tous les champs
    }
    handleSubmit=(e)=>{
        e.preventDefault();
        const {login, pass} = this.state;
        if(login!== "" && pass!== ""){
            if(login === 'Raphael' && pass ==='123'){
                const d = new Date();
                const cle = d.toLocaleDateString();
                sessionStorage.setItem('masterKey',cle);
                this.props.history.push('/admin123');
                window.location.reload(); //Avec cette ligne plus besoin de rafraichir pour faire apparaitre deconnexion
            }else{
                const fail = <div className="alert alert-warning text-center fw-bolder">Le login et/ou le mot de passe ne correspondent pas</div>
                this.setState({errorMessage: fail});
            }

        }else{
            const error = <div className="alert alert-danger text-center fw-bolder">Veuillez saisir les deux champs maintenant!!!</div>
            this.setState({errorMessage: error});
        }
    }
    render() { 
        return ( 
            <>
                  <div className="container mt-5 offset-12  col-12" style={{height:"550px"}}>
            <div className="card col-4 offset-4 " >
            {this.state.errorMessage}
                {/* {this.state.errorMessage && <div className="alert alert-danger">{this.state.errorMessage}</div>} */}
                <div className="card-header text-center h4" >Formulaire de connexion</div>
                    <div className="card-body">
                        <form onSubmit={this.handleSubmit} style={{height:"350px" ,marginTop:"50px"}}>
                                <div className="mb-5">
                                    <label htmlFor="login" className="form-label">Login</label>
                                    <input type="text" onChange={this.handleChange} className="form-control" id="login" name="login" placeholder="Entrez votre identifiant..."/>
                                </div>
                                {this.state.loginError}
                                <div className="mb-5">
                                    <label htmlFor="pass" className="form-label">Mot de passe</label>
                                    <input type="password" onChange={this.handleChange} className="form-control" id="pass" name="pass" placeholder="Entrez votre mot de passe..."/>
                                </div>
                                {this.state.passError}
                            <button type="submit" className="btn btn-primary col-12">Soumettre</button>
                        </form>
                    </div>
                </div>
            </div>
            </>
         );
    }
}
 
export default Login;