import React, { Component } from 'react';
import Add from './Add';
import Listo from './Listo';
import Modif from './Modif';

class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            films:[
                {   id:1, 
                    titre:'Jurassic Park', 
                    realisateur:'Steven SPIELBERG', 
                    nationalite:'USA', 
                    genre:'Action', 
                    image:'futur.jpg',
                    synopsis:'Ne pas réveiller le chat qui dort... C\'est ce que le milliardaire John Hammond aurait dû se rappeler avant de se lancer dans le "clonage" de dinosaures. C\'est à partir d\'une goutte de sang absorbée par un moustique fossilisé que John Hammond et son équipe ont réussi à faire renaître une dizaine d\'espèces de dinosaures.',
                    acteurs:'Avec Sam Neill, Laura Dern, Jeff Goldblum', 
                    prix:5000, 
                    disponible:true},
                
                {   id:2, 
                    titre:'De l\'ombre à la lumière', 
                    realisateur:'Ron HOWARD', 
                    nationalite:'USA', 
                    genre:'Drame/Sport', 
                    image:'vandamme.jpg',
                    synopsis: 'Autrefois boxeur prometteur, Jim Braddock s\'est vu contraint d\'abandonner la compétition après une série de défaites. Alors que l\'Amérique sombre dans la Grande Dépression, Jim accepte n\'importe quel petit boulot pour faire vivre sa femme Mae et leurs enfants. Il n\'abandonne pourtant pas l\'espoir de remonter un jour sur le ring.',
                    acteurs:'Avec Russell Crowe, Renée Zellweger, Paul Giamatti',
                    prix:500, 
                    disponible:false},

                {   id:3, 
                    titre:'Pulp Fiction', 
                    realisateur:'Quentin TARANTINO', 
                    nationalite:'USA', 
                    genre:'Action', 
                    image:'lordofthering.jpg', 
                    acteurs:'Avec John Travolta, Samuel L. Jackson, Uma Thurman',
                    synopsis:'L\'odyssée sanglante et burlesque de petits malfrats dans la jungle de Hollywood à travers trois histoires qui s\'entremêlent.',
                    prix:5000, 
                    disponible:false},
                {   id:4, 
                    titre:'Goonies', 
                    realisateur:'Richard DONNER', 
                    nationalite:'USA', 
                    genre:'Famille', 
                    image:'starwars.jpg',
                    acteurs:'Avec Sean Astin, Josh Brolin, Corey Feldman',
                    synopsis: 'Astoria est une modeste et paisible cité portuaire de la Côte Ouest. Trop paisible au gré de ses jeunes habitants... "Il ne se passe jamais rien ici", soupira un jour l\'un d\'eux, mélancolique.',
                    prix:9000, 
                    disponible:true}
            ],
            indexMod: -1,
            modiForm:false,
            modifMovie: {id:-1, titre:'', realisateur:'', nationalite:'', genre:'', prix:0, acteurs:'', synopsis:'', image:'', disponible:false}
         }
    }
    componentDidMount=()=>{
        let films1 = JSON.parse(localStorage.getItem('filmsKey'));//( Json en caractere)
       
        if(!films1 || films1.length === 0){
        localStorage.setItem('filmsKey', JSON.stringify(this.state.films));//(du caractere en Json)
        let filmsRecup = JSON.parse(localStorage.getItem('filmsKey'));
         this.setState({films: filmsRecup});

       }else{
           let films2 = JSON.parse(localStorage.getItem('filmsKey'));
           this.setState({films:films2})
       }
    }
   
    handleRemove=(data)=>{
        console.log( data)
        let newFilms=[...this.state.films];
        const movies= newFilms.filter((f, id)=>{
            return id !== data;
        });
        console.log(movies)
        this.setState({films:movies},()=>{
             localStorage.setItem('filmsKey', JSON.stringify(this.state.films));
            });
    }

    handleShow = (etat)=>{
        this.setState({modiForm:true})
    }

    handleFilm=(movie)=>{
        movie.id = this.state.films[this.state.films.length-1].id + 1;
        let copyFilms = [...this.state.films, movie]
        this.setState({films:copyFilms},()=>{
            localStorage.setItem('filmsKey',JSON.stringify(this.state.films))
        });

        console.log(movie)
    }
    
     handleDvd =(index)=>{
        const dvdColl = this.state.films[index];//Pour recuperer tous les indices du tableau index(2)
        this.setState({modifMovie:dvdColl, indexMod:index});
         console.log(index);
     }

    handleModif=(data)=>{
        let filmModif= [...this.state.films];
            filmModif[this.state.indexMod]=data;
            this.setState({films:filmModif},()=>{
                localStorage.setItem('filmsKey',JSON.stringify(this.state.films))
            });
            console.log(data);
    }

    render() { 
        
        return ( 
            <>
                    <h1 className=" text-white text-center mt-5">Back-Office</h1>
                {
                    (!this.state.modiForm && this.state.indexMod === -1)
                    && <button onClick={this.handleShow} className="btn btn-primary text-white offset-10  mb-2 ">Ajouter</button>
                }
                    <div className="container">
                         {  (!this.state.modiForm && this.state.indexMod > -1)
                            ?<Modif filmShared = {this.state.modifMovie} modifUp={this.handleModif}/>
                            :(this.state.modiForm)
                                ?<Add getFilms={this.handleFilm}/> 
                                :<Listo lines={this.state.films} handleDelete={this.handleRemove} handleRecorrect={this.handleDvd}/>
                        }
                    </div>   
                    
                  
            </>

         );
    }
}
 
export default Admin;
