import React from 'react';
import { FaCartPlus, FaCcMastercard } from 'react-icons/fa';
import { Scrollbars } from 'rc-scrollbars';


const Accueil = () => {
   
    const films= JSON.parse(localStorage.getItem('filmsKey'));
   
    return ( 
        <>  
            <div className="bg-ligth p-5" >
                <div className="container text-center ">
                    <h1 className="display-3" >Coffret Collector Edition Illimité </h1>
                    <p className="display-6">Pour une expérience unique</p>
                </div>
            </div>

         <div className="container offset-2 ">
                <div className="row row-cols-1 row-cols-md-2 g-4 mt-3 col-10">
                {films.map((film, index)=>{
                            return <div className="">
                                        <div className="card">
                                            <img src={`images/${film.image}`} className="card-img-top" alt="..." />
                                            <div className="card-body">
                                                <h5 className="card-title text-center"><u>Coffret Collector Blu-ray N° 00{film.id}</u></h5>
                                                <ul className="list-group list-group-flush">
                                                    <li className="list-group-item"><i>Titre:</i> <strong>{film.titre}</strong></li>
                                                    <li className="list-group-item"><i>Réalisateur:</i>  {film.realisateur}</li>
                                                    <li className="list-group-item"><i>Nationalité:</i>  {film.nationalite}</li>
                                                    <li className="list-group-item"><i>Genre:</i>  {film.genre} </li>
                                                    <li className="list-group-item"><i>Acteurs:</i><Scrollbars style={{ width: 400, height: 50 }}>  {film.acteurs}</Scrollbars> </li>
                                                    <li className="list-group-item"><i>Synopsis:</i><Scrollbars style={{ width: 400, height: 50 }}> {film.synopsis} </Scrollbars> </li>
                                                    <li className="list-group-item"><i>Prix:</i>  {film.prix} €</li>
    
                                                </ul>
                                                {film.disponible// mettre data toggle... pour obtenir la faculté du modal lorsqu'on clique dessus et mettre l'import dans app
                                                    ?<button className="btn btn-success mt-1"  data-bs-toggle="modal" data-bs-target={`#film${index}`} ><FaCartPlus/> Commander</button>
                                                    :<button className="btn btn-secondary mt-1" disabled="true"><FaCartPlus/> Indisponible!</button>
                                                }
                                                <div className="modal fade" id={`film${index}`} tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                                    <div className="modal-dialog">
                                                        <div className="modal-content">
                                                            <div className="modal-header bg-warning">
                                                                <h5 className="modal-title bg-warning" id="exampleModalLabel">{film.titre}</h5>
                                                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                                            </div>
                                                                <div className="modal-body">
                                                                        <div className="card mb-3">
                                                                            <div className="row g-0">
                                                                                <div className="col-md-4">
                                                                                    <img src={`images/${film.image}`} width="150"  alt="..."/>
                                                                                </div>
                                                                                    <div className="col-md-8">
                                                                                        <div className="card-body">
                                                                                            <h5 className="card-title">Paiement du coffret  {film.id}</h5>
                                                                                            <form>
                                                                                                <div className="mb-1">
                                                                                                    <label htmlFor="nom" className="form-label">Nom de la carte <FaCcMastercard/></label>
                                                                                                    <input type="text" className="form-control" id="nom" placeholder="Entrez votre nom"/>
                                                                                                </div>
                                                                                                <div className="mb-1">
                                                                                                    <label htmlFor="adresse" className="form-label">Numero de carte</label>
                                                                                                    <input type="text" className="form-control mb-1" id="nc" placeholder="Votre numero de carte"/>
                                                                                                </div>
                                                                                                <div className="mb-1">
                                                                                                    <label htmlFor="adresse" className="form-label">Date de validité</label>
                                                                                                    <input type="date" className="form-control mb-1" id="dv" placeholder="Date de validité"/>
                                                                                                </div>
                                                                                                <div className="mb-1">
                                                                                                    <label htmlFor="adresse" className="form-label">Cryptogramme</label>
                                                                                                    <input type="text" className="form-control mb-1" id="cr" placeholder="votre cryptogramme"/>
                                                                                                </div>
                                                                                                <button type="submit" className="btn btn-success" data-bs-dismiss="modal">Valider!</button>
                                                                                            </form>
                                                                                        </div>
                                                                                    </div>
                                                                         </div>
                                                                    </div>
                                                                </div>
                                                              {/* <div className="modal-footer">
                                                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button> 
                                                                <button type="button" className="btn btn-primary">Save changes</button>
                                                            </div> */}
                                                         </div>
                                                    </div>
                                                </div> 
                                            </div>
                                        </div>
                                    </div>
                        })}
                
                </div>
         </div>
        </>
     );
}
 
export default Accueil;