import React from 'react';
import { BrowserRouter, Route, Switch, NavLink, Redirect } from 'react-router-dom';
import { FaUserLock, FaUserTimes } from 'react-icons/fa';
import Admin from './Admin';
import Accueil from './Pages/Accueil';
import Contact from './Pages/Contact';
import Not_found404 from './Pages/Not_found404';
import Presentation from './Pages/Presentation';
import Login from './Login';
import Logout from './Logout';

const authenticate =()=>{
  const d = new Date();
  const test = d.toLocaleDateString();
  if(test === sessionStorage.getItem('masterKey')){
    return true;
  }else{
    return false;
  }
  //Pour donner accès au tableau  avec le login et le pass
}

const Menu = () => {
    return ( 
        <>
         <BrowserRouter basename="/Project-react">
          <nav>
            <nav className="navbar navbar-expand-lg navbar-dark bg-info">
              <div className="container-fluid">
                <NavLink className="navbar-brand display-2" activeClassName="active" to="/" exact><img src="images/cheese.png"/></NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarText" aria-controls="navbarText" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarText">
                  <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                    <li className="nav-item">
                      <NavLink className="nav-link " activeClassName="active" aria-current="page" to="/" exact>Accueil</NavLink>
                    </li>
                    <li className="nav-item">
                      <NavLink className="nav-link" activeClassName="active" to="/presentation">Présentation</NavLink>
                    </li>
                    <li className="nav-item">
                      <NavLink className="nav-link" activeClassName="active" to="/contact">Contact</NavLink>
                    </li>
                    {/* <li className="nav-item offset-10 ">
                      <NavLink className="nav-link " activeClassName="active " to="/admin">Admin</NavLink>
                    </li> */}
                  </ul>
                     <span className="navbar-text">
                      {!authenticate()
                        ?<NavLink className="nav-link" activeClassName="active" to="/login"><FaUserLock/> Connexion</NavLink>
                        :<NavLink className="nav-link" activeClassName="active" to="/logout"><FaUserTimes/> Déconnexion</NavLink>
                      }
                    
                    </span>
                </div>
                </div>
            </nav>
        </nav>
        <Switch>
          
          <Route path='/' component={Accueil} exact />
          <Route path='/presentation' component={Presentation} />
          <Route path='/contact' component={Contact} />
          {/* <Route path='/admin' component={Admin}/> */}
          <Route path='/login' component={Login}/>
          <Route path='/logout' component={Logout}/>

          <Redirect from='/admin123/reload' to="/admin123"/>
          <Route path='/admin123' render={()=>(
            authenticate()
            ?<Admin/>
            :<Redirect to="/login"/>
            )}  />
          <Route path='*' component={Not_found404}  />
        </Switch>

      </BrowserRouter>


        </>
     );
}
 
export default Menu;